--><html><head></head><body><h1>TencentImageryProvider</h1>
<p><code v-pre="">vc-provider-imagery-tencent</code> 组件用于高德栅格瓦片地图服务。</p>
<h2>示例</h2>
<h3>加载腾讯地图影像服务图层</h3>
