          </md-option>
        </md-select>
        <span>切换服务</span>
        <md-select v-model="styleId" placeholder="请选择">
          <md-option v-for="item in styleIdOptions" :key="item.value" :label="item.label" :value="item.value">
            {{item.label}}
          </md-option>
        </md-select>
      </div>
    </div>
  </template>
  
