<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>string</td>
<td></td>
<td><code v-pre="">optional</code> 指定服务地址。</td>
<td></td>
</tr>
<tr>
<td>subdomains</td>
<td>Array&lt;string&gt;</td>
<td><code v-pre="">['1', '2', '3']</code></td>
<td><code v-pre="">optional</code> 指定服务轮询参数。</td>
<td></td>
</tr>
<tr>
<td>mapStyle</td>
<td>string</td>
<td><code v-pre="">'vector'</code></td>
<td><code v-pre="">optional</code> 指定腾讯地图服务地图风格类型。'img': 卫星影像; 'terrain': 地形图; 'vector': 道路图</td>
<td>img/terrain/vector</td>
</tr>
<tr>
<td>styleId</td>
<td>string</td>
<td><code v-pre="">'1'</code></td>
<td>指定风格id，mapStyle 为 vector 时有效。1: 经典; 2: 标签; 3: 标签; 4: 墨渊; 8: 白浅; 9: 灰色</td>
<td>1/2/3/4/8/9</td>
</tr>
<tr>
<td>protocol</td>
<td>string</td>
<td></td>
<td><code v-pre="">optional</code> 指定服务协议。</td>
<td>http/https</td>
</tr>
<tr>
<td>credit</td>
<td>string|Cesium.Credit</td>
<td><code v-pre="">''</code></td>
<td><code v-pre="">optional</code> 服务版权描述信息。</td>
<td></td>
</tr>
<tr>
<td>rectangle</td>
<td>VcRectangle</td>
<td></td>
<td><code v-pre="">optional</code> 图层的矩形范围，此矩形限制了影像可见范围。</td>
<td></td>
</tr>
<tr>
<td>minimumLevel</td>
<td>number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 最小层级。</td>
<td></td>
</tr>
<tr>
<td>maximumLevel</td>
<td>number</td>
<td><code v-pre="">20</code></td>
<td><code v-pre="">optional</code> 最大层级。</td>
<td></td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Cesium.GeographicTilingScheme | Cesium.WebMercatorTilingScheme</td>
<td><code v-pre="">new Cesium.WebMercatorTilingScheme()</code></td>
<td><code v-pre="">optional</code> 指定将影像瓦片展开到地球的投影方案。</td>
<td></td>
</tr>
<tr>
<td>projectionTransforms</td>
<td>boolean|ProjectionTransforms</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 指定投影变换参数。<strong>结构： { from: 'GCJ02', to: 'WGS84' }</strong></td>
<td></td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层的提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层可用时触发, 返回 ImageryProvider 实例。</td>
</tr>
</tbody>
</table>
