<script>
    export default {
      data() {
        return {
          mapStyleOptions: [
            {
              value: 'img',
              label: '卫星影像'
            },
            {
              value: 'terrain',
              label: '地形图'
            },
            {
              value: 'vector',
              label: '道路图'
            }
          ],
          styleIdOptions: [
            {
              value: '1',
              label: '经典'
            },
            {
              value: '2',
              label: '标签1'
            },
            {
              value: '3',
              label: '标签2'
            },
            {
              value: '4',
              label: '墨渊'
            },
            {
              value: '8',
              label: '白浅'
            },
            {
              value: '9',
              label: '浅灰'
            }
          ],
          mapStyle: 'vector',
          styleId: '1',
          alpha: 1,
          brightness: 1,
          contrast: 1,
          projectionTransforms: {
            form: 'BD09',
            to: 'WGS84'
          },
          camera: {
            position: {
              x: -1336250.5076601694,
              y: 5333101.760774733,
              z: 3229877.420756886
            },
            heading: 6.259802922203329,
            pitch: -1.5382656190477708,
            roll: 0
          }
        }
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.Cesium = Cesium
          this.viewer = viewer
          window.vm = this
          window.viewer = viewer
        }
      }
    }
  </script>